import { PageConfig } from '@backbase/lib-bb-page-bootstrap-cdn';
import { InjectionToken } from '@angular/core';
import { WebSdkServiceMap } from './providers';

/**
 * @deprecated Will be removed in v7.0.0
 */
export const PAGE_CONFIG = new InjectionToken<PageConfig>('web-sdk-page-config');

const defaultPageConfig: PageConfig = {
  apiRoot: '/gateway/api/',
  staticResourcesRoot: '/',
  currentLink: '',
  pageName: '',
  portalName: '',
  version: '6',
  locale: 'en-US',
  locales: ['en-US'],
  currentLinkUUID: '',
  designmode: false,
  cspNonce: '',
  linkRoot: '/gateway',
  webSdkVersion: '',
  userId: '',
};

/**
 * @deprecated Will be removed in v7.0.0
 */
export function getPageConfig(webSdkServiceMap: WebSdkServiceMap = {}): PageConfig {
  const pageConfig = webSdkServiceMap.pageConfig
    ? { ...defaultPageConfig, ...webSdkServiceMap.pageConfig() }
    : defaultPageConfig;

  return pageConfig;
}
