import { Injectable } from '@angular/core';
import { PubsubListener } from './public-api';

@Injectable({
  providedIn: 'root',
})
export class PubSubService {
  private listenersMap: Map<string, Array<PubsubListener<any>>>;

  private getListeners = (string: string): Array<PubsubListener<any>> => this.listenersMap.get(string) || [];

  constructor() {
    this.listenersMap = new Map<string, Array<PubsubListener<any>>>();
  }

  publish<T>(string: string, payload: T): void {
    const listeners = this.getListeners(string);
    listeners.forEach(listener => {
      try {
        listener(payload);
      } catch (error) {
        console.error(error);
      }
    });
  }

  subscribe<T>(string: string, listener: PubsubListener<T>): () => void {
    const listeners = this.getListeners(string);
    const newListeners = [...listeners, listener];
    this.listenersMap.set(string, newListeners);
    return () => {
      this.unsubscribe<T>(string, listener);
    };
  }

  unsubscribe<T>(string: string, listener: PubsubListener<T>): void {
    const listeners = this.getListeners(string);
    const newListeners = listeners.filter(currentListener => currentListener !== listener);
    this.listenersMap.set(string, newListeners);
  }

  unsubscribeAll(): void {
    this.listenersMap.clear();
  }
}
