export type CountdownRegister = (countdown: Countdown) => () => void;

/**
 * @deprecated Will be removed in v7.0.0
 * @description
 * Token to get lean-client services map directly.
 * Used internally by foundation-ang to register lean-client services into the angular app.
 */

export interface ReauthenticateService {
  /**
   * @description method to reauthenticate a user
   * @param {string} scope identifies the resource or permission that the user is consenting to.
   * @param {string} acrValues a space-delimited list of acceptable levels of assurance.
   *                  Can be used by the IdP to steer the authentication flow.
   * @param {ReauthConfig} [reauthConfig] A configuration allowing the passing of optional parameters.
   * @return {Promise}
   */
  reauthenticate: Reauthenticate;
}

/**
 * @deprecated Will be removed in v7.0.0
 * @description
 * Service to register custom countdown callbacks, to refresh session, to get time to live of the session.
 *
 * @example
 * export class CountdownService {
 *   constructor(
 *     @Inject(SESSION) private sessionService: SessionService,
 *     private modalService: ModalService,
 *   ) {
 *     this.registerCountdown();
 *   }
 *
 *   private registerCountdown() {
 *     this.sessionService.registerCountdown({
 *       start: () => {
 *         this.modalService.open({
 *           header: 'Session notification',
 *           message: 'Your session will expire soon',
 *           okHandler: () => this.sessionService.refresh()
 *         });
 *       },
 *       end: () => {
 *         this.modalService.open('session', {
 *           header: 'Session notification',
 *           question: 'Your session is expired! Do you want to log in?',
 *           okHandler: () => this.sessionService.refresh(),
 *         });
 *       },
 *       reset: () => {
 *         this.modalService.close();
 *       },
 *       duration: 20, //seconds
 *     });
 *   }
 * }
 */
export interface SessionService {
  /**
   * @description Refreshes session
   * @return {Promise} promise that will be resolved when session is refreshed
   */
  refresh: RefreshSession;
  /**
   * @description Registers countdown callbacks.
   * @param {Countdown} countdown
   */
  registerCountdown: CountdownRegister;
  /**
   * @description Returns time to live of the session
   * @return {number} time to live of the session (seconds)
   */
  timeToLive: TimeToLive;
  /**
   * @description Whether the user is being impersonated
   */
  isImpersonated: boolean;
}

/**
 * This returns a BackbaseAuth, but with the `keysToCheck` as required, not optional.
 */
function getAuth<K extends keyof BackbaseAuth, T extends Required<Pick<BackbaseAuth, K>>>(
  serviceName: string,
  keysToCheck: Array<K>,
  externalServices?: WebSdkServiceMap,
  optionalKeys?: Array<K>,
): T {
  const auth = websdkServiceFactory(externalServices, 'auth');
  if (!auth) {
    throw new Error('Authentication Service is not available.');
  }
  keysToCheck.forEach((key: K) => {
    if (!auth[key] && !optionalKeys?.includes(key)) {
      throw new Error(
        `'${serviceName}' Service cannot be injected because '${key}' is not available in authentication '${auth.config.kind}' strategy.`,
      );
    }
  });
  return (auth as unknown) as T; // this is guaranteed by the above checks
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export function loginFactory(webSdkServiceMap?: WebSdkServiceMap): LoginService {
  const auth = getAuth('Login', ['login'], webSdkServiceMap);
  return {
    login: auth.login,
  };
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export function logoutFactory(webSdkServiceMap?: WebSdkServiceMap): LogoutService {
  const auth = getAuth('Logout', ['logout', 'goToLoginPage'], webSdkServiceMap);
  return {
    logout: auth.logout,
    goToLoginPage: auth.goToLoginPage,
  };
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export function sessionFactory(webSdkServiceMap?: WebSdkServiceMap): SessionService {
  const auth = getAuth('Session', ['register', 'refresh', 'timeToLive', 'isImpersonated'], webSdkServiceMap, [
    'isImpersonated',
  ]);
  return {
    registerCountdown: auth.register,
    refresh: auth.refresh,
    timeToLive: auth.timeToLive,
    isImpersonated: auth.isImpersonated ? auth.isImpersonated() : false,
  };
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export function reauthenticateFactory(webSdkServiceMap?: WebSdkServiceMap): ReauthenticateService {
  const auth = getAuth('Reauthenticate', ['reauthenticate'], webSdkServiceMap);
  return {
    reauthenticate: auth.reauthenticate,
  };
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export function getAuthEvents(webSdkServices: WebSdkServiceMap): AuthEvents {
  if (!webSdkServices.auth || !webSdkServices.eventBus) {
    throw new Error('Unable to listen to auth events');
  }
  // @todo: remove 'as any' when web-sdk updates BackbaseAuth
  return wrapAuthEvents(webSdkServices.auth() as any, webSdkServices.eventBus());
}
