import { InjectionToken } from '@angular/core';

import {
  Navigation,
  Pubsub,
  PortalContent,
  BackbaseAuth,
  HttpInterceptors,
  Refresh,
  SetLocale,
  PageConfig,
} from '@backbase/lib-bb-page-bootstrap-cdn';
import { PropertyResolver } from '@backbase/foundation-ang/entitlements';

/**
 * @deprecated Will be removed in v7.0.0. Use the `PubSubService` class instead.
 */
export const PUBSUB = new InjectionToken<Pubsub>('web-sdk-pubsub');
/**
 * @deprecated Will be removed in v7.0.0. Use the `SetLocaleService` class instead.
 */
export const SET_LOCALE = new InjectionToken<SetLocale>('web-sdk-set-locale');

/**
 * @deprecated Will be removed in v7.0.0
 */
export interface WebSdkServiceMap {
  pageConfig?: () => PageConfig;
  navigation?: () => Navigation;
  eventBus?: () => Pubsub;
  portalContent?: () => PortalContent;
  auth?: () => BackbaseAuth;
  httpInterceptors?: () => HttpInterceptors;
  refresh?: () => Refresh;
  setLocale?: () => SetLocale;
  conditions?: () => Pick<PropertyResolver, 'resolveEntitlements'>;
}

/**
 * @deprecated Will be removed in v7.0.0
 */
export interface ExternalServices {
  [key: string]: () => any;
}
/**
 * @deprecated Will be removed in v7.0.0
 */
export const EMBEDDED_SERVICES = new InjectionToken<WebSdkServiceMap>('embedded platform services');

/**
 * @description
 * Service to reauthenticate a user.
 *
 * @example
 * export class SigningService {
 *   constructor(
 *     @Inject(REAUTHENTICATE) private reauthenticateService: ReauthenticateService,
 *   ) {
 *     this.reauthenticate(error);
 *   }
 *
 *   private reauthenticate(error: HttpErrorResponse) {
 *     const { acrValues, scope } = this.getChallenge(error);
 *     this.reauthenticateService.reauthenticate(scope, acrValues);
 *   }
 *
 *   private getReauthChallenge(error: HttpErrorResponse) {
 *     const challenges = error.error ? error.error.challenges : [];
 *     return challenges.find(c => c.scope !== undefined);
 *   }
 * }
 */

/**
 * @deprecated Will be removed in v7.0.0
 */
export const EXTERNAL_SERVICES = new InjectionToken<WebSdkServiceMap>('external platform services');
/**
 * @deprecated Will be removed in v7.0.0
 */
export const WEBSDK_SERVICE_MAP = new InjectionToken<WebSdkServiceMap>('Web SDK Service Map');

/**
 * @deprecated Will be removed in v7.0.0
 */
export function websdkServiceFactory<
  K extends keyof WebSdkServiceMap,
  T extends ReturnType<Required<WebSdkServiceMap>[K]>
>(webSdkServiceMap: WebSdkServiceMap | undefined, serviceKey: K): T | undefined {
  if (!webSdkServiceMap) {
    return undefined;
  }
  const factory = webSdkServiceMap[serviceKey];
  if (!factory) {
    return undefined;
  }
  return factory() as T; // TS isn't smart enough to know that factory() is T.
}

/**
 * @deprecated Will be removed in v7.0.0
 */
export function navigationFactory(webSdkServiceMap?: WebSdkServiceMap): Navigation | undefined {
  return websdkServiceFactory(webSdkServiceMap, 'navigation');
}

/**
 * @deprecated Will be removed in v7.0.0
 */
export function pubsubFactory(webSdkServiceMap?: WebSdkServiceMap): Pubsub | undefined {
  return websdkServiceFactory(webSdkServiceMap, 'eventBus');
}

/**
 * @deprecated Will be removed in v7.0.0
 */
export function portalContentFactory(webSdkServiceMap?: WebSdkServiceMap): PortalContent | undefined {
  return websdkServiceFactory(webSdkServiceMap, 'portalContent');
}

/**
 * @deprecated Will be removed in v7.0.0
 */
export function setLocaleFactory(webSdkServiceMap?: WebSdkServiceMap): SetLocale | undefined {
  return websdkServiceFactory(webSdkServiceMap, 'setLocale');
}
